import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { fetchQuizzes, fetchUsers, increaseMark } from '../../actions'

import Header from './Header'

class ShowQuiz extends React.Component {
  constructor() {
    super()
    this.state = { view:null }
  }

  componentDidMount() {
    this.props.dispatch(fetchQuizzes())
  }

  render() {
    return (
      <>
        <Header />
        <div className='wrapper'>
          <div className='top-container'></div>
          <div className='main-container'>
            <span className='score'>Marks: {this.props.user.mark}</span>
            <div className='tag-container'>
              {this.props.quiz.quizzes ? (
                <div>
                  {[...new Set(this.props.quiz.quizzes.quizzes)].map((val, ind) => (
                    <div
                      className='tag'
                      onClick={() => {
                        this.setState({ view: val.quizset })
                      }}
                    >
                      {val.quizset}
                    </div>
                  ))}
                </div>
              ) : (
                <div>Loading...</div>
              )}
            </div>
            {/*  */}
            <div className='card-container'>
              {
                this.state.view == null?(
                  this.props.quiz.quizzes
                ? this.props.quiz.quizzes.quizzes.map((val, ind) => (
                    <div className='card'>
                      <h1>{val.question}</h1>
                      <h2
                        onClick={() => {
                          val.option1 === val.answer
                            ? this.props.dispatch(increaseMark())
                            : alert('Wrong')
                        }}
                      >
                        A: {val.option1}
                      </h2>
                      <h2
                        onClick={() => {
                          val.option2 === val.answer
                            ? this.props.dispatch(increaseMark())
                            : alert('Wrong')
                        }}
                      >
                        B: {val.option2}
                      </h2>
                      <h2
                        onClick={() => {
                          val.option3 === val.answer
                            ? this.props.dispatch(increaseMark())
                            : alert('Wrong')
                        }}
                      >
                        C: {val.option3}
                      </h2>
                      <h2
                        onClick={() => {
                          val.option4 === val.answer
                            ? this.props.dispatch(increaseMark())
                            : alert('Wrong')
                        }}
                      >
                        D: {val.option4}
                      </h2>
                    </div>
                  ))
                : ''
                ):(
                  this.props.quiz.quizzes
                ? this.props.quiz.quizzes.quizzes.map((val, ind) => (
                    val.quizset == this.state.view ?(<div className='card'>
                      <h1>{val.question}</h1>
                      <h2
                        onClick={() => {
                          val.option1 === val.answer
                            ? this.setState({ score: ++this.state.score })
                            : alert('Wrong')
                        }}
                      >
                        A: {val.option1}
                      </h2>
                      <h2
                        onClick={() => {
                          val.option2 === val.answer
                            ? this.setState({ score: ++this.state.score })
                            : alert('Wrong')
                        }}
                      >
                        B: {val.option2}
                      </h2>
                      <h2
                        onClick={() => {
                          val.option3 === val.answer
                            ? this.setState({ score: ++this.state.score })
                            : alert('Wrong')
                        }}
                      >
                        C: {val.option3}
                      </h2>
                      <h2
                        onClick={() => {
                          val.option4 === val.answer
                            ? this.setState({ score: ++this.state.score })
                            : alert('Wrong')
                        }}
                      >
                        D: {val.option4}
                      </h2>
                    </div>):""
                  ))
                : ''
                )
              }
            </div>
          </div>
        </div>
      </>
    )
  }
}

function mapStateToProps(state) {
  return state
}

export default connect(mapStateToProps)(withRouter(ShowQuiz))
