import { SET_QUIZZES, DELETE_QUIZ } from '../types'

let INITIAL_STATE = {
  quizzes: null
}

function quizReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case SET_QUIZZES:
      return {
        ...state,
        quizzes: action.payload
      }

    case DELETE_QUIZ:
      const filteredQuizzes = state.quizzes.quizzes.filter(val => {
        if (!(val._id === action.payload)) {
          return val
        }
      })
      console.log(filteredQuizzes)

      return {
        ...state,
        quizzes: { quizzes: filteredQuizzes }
      }

    default:
      return state
  }
}

export default quizReducer
