import { SET_USERS, INCREASE_MARK } from '../types'

let INITIAL_STATE = {
  user: null,
  mark: 0
}

function userReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case SET_USERS:
      return {
        ...state,
        user: action.payload
      }

    case INCREASE_MARK:
      state.mark = ++state.mark
      return {
        ...state
      }
    default:
      return state
  }
}

export default userReducer
