import React from 'react'
import { withRouter } from 'react-router-dom'

class QuizEdit extends React.Component {
  constructor() {
    super()
    this.state = {
      question: '',
      quizset: '',
      option1: '',
      option2: '',
      option3: '',
      option4: '',
      answer: ''
    }
  }

  handleSubmit = event => {
    event.preventDefault()
    fetch(`/api/v1/quizzes/${this.props.match.params.id}`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.token
      },
      body: JSON.stringify({
        question: this.state.question,
        quizset: this.state.quizset,
        option1: this.state.option1,
        option2: this.state.option2,
        option3: this.state.option3,
        option4: this.state.option4,
        answer: this.state.answer
      })
    })
      .then(res => res.json())
      .then(res => {
        if (res.msg) {
          alert(res.msg)
        }
      })
  }

  componentDidMount() {
    fetch(`/api/v1/quizzes/${this.props.match.params.id}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.token
      }
    })
      .then(res => res.json())
      .then(res => this.setState(res))
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value })
  }

  render() {
    return (
      <div className='wrapper'>
        <div>
          <form onSubmit={this.handleSubmit} className='create-quiz'>
            <label className='crate-new-label' htmlFor='question'>
              Question:
            </label>
            <input
              className='crate-new-label'
              type='text'
              name='question'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.question}
            />
            <label className='crate-new-label' htmlFor='quizset'>
              Quizset:
            </label>
            <input
              type='text'
              name='quizset'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.quizset}
            />
            <label className='crate-new-label' htmlFor='option1'>
              Option1:
            </label>
            <input
              type='text'
              name='option1'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.option1}
            />
            <label className='crate-new-label' htmlFor='option2'>
              Option2:
            </label>
            <input
              type='text'
              name='option2'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.option2}
            />

            <label
              className='crate-new-label'
              htmlFor='option3'
              onChange={this.handleChange}
            >
              Option3:
            </label>
            <input
              type='text'
              name='option3'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.option3}
            />
            <label className='crate-new-label' htmlFor='option4'>
              Option4:
            </label>
            <input
              type='text'
              name='option4'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.option4}
            />
            <label className='crate-new-label' htmlFor='answer'>
              Answer:
            </label>
            <input
              type='text'
              name='answer'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.answer}
            />
            <button className='btn' type='submit'>
              Submit
            </button>
          </form>
        </div>
      </div>
    )
  }
}

export default withRouter(QuizEdit)
