import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import { fetchQuizzes, deleteQuiz } from '../../actions'
import AdminHeader from './AdminHeader'

class ListQuizCreated extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  handleEdit = id => {
    this.props.history.push('/admin/quiz/edit/' + id)
  }

  handleDelete = id => {
    this.props.dispatch(deleteQuiz(id))
  }

  componentDidMount() {
    this.props.dispatch(fetchQuizzes())
  }

  render() {
    return (
      <>
        <AdminHeader />
        <div className='wrapper'>
          <div className='main-container'>
            {this.props.quizzes && this.props.quizzes.quizzes ? (
              this.props.quizzes.quizzes.map((val, ind) => (
                <div key={ind} className='card'>
                  <div className='quizset'>{val.quizset}</div>
                  <div>{val.question}</div>
                  <div className='btn-div'>
                    <span
                      className='delete'
                      onClick={() => {
                        this.handleDelete(val._id)
                      }}
                    >
                      Delete
                    </span>
                    <span
                      className='edit'
                      onClick={() => {
                        this.handleEdit(val._id)
                      }}
                    >
                      Edit
                    </span>
                  </div>
                </div>
              ))
            ) : (
              <center>"Nothing To Show..."</center>
            )}
          </div>
        </div>
      </>
    )
  }
}

function mapStateToProps(state) {
  return state.quiz
}

export default connect(mapStateToProps)(withRouter(ListQuizCreated))
