import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import { setUsers } from '../../actions'

class Auth extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      viewAuth: 'signin',
      viewTo: 'users',
      username: '',
      password: '',
      email: ''
    }
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value })
  }

  handleRequest = () => {
    if (this.state.viewAuth === 'signup') {
      fetch(`/api/v1/${this.state.viewTo}`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          user: {
            username: this.state.username,
            email: this.state.email,
            password: this.state.password
          }
        })
      })
        .then(res => res.json())
        .then(res => {
          if (res.msg) {
            alert(res.msg)
          }
        })
    }

    if (this.state.viewAuth === 'signin') {
      fetch(`/api/v1/${this.state.viewTo}/login`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          user: {
            email: this.state.email,
            password: this.state.password
          }
        })
      })
        .then(res => res.json())
        .then(res => {
          if (res.msg) {
            alert(res.msg)
          }
          if (res.token) {
            localStorage.setItem('token', res.token);
            this.props.dispatch(setUsers(this.state.viewTo))
          }
          if (res.id) {
            localStorage.setItem('id', res.id)
          }

          if (this.state.viewTo === 'admins') {
            this.props.history.push('/admin/quiz/created')
          }

          if (this.state.viewTo === 'users') {
            this.props.history.push('/')
          }

        })
    }
  }

  displayInput = () => {
    switch (this.state.viewAuth) {
      case 'signup':
        return (
          <>
            <div className='input-effect col-3 '>
              <label>Full Name</label>
              <input
                name='username'
                type='text'
                className='style-input'
                onChange={this.handleChange}
              />
              <span className='focus-border'></span>
            </div>
            <div className='input-effect col-3 '>
              <label>E-Mail</label>
              <input
                name='email'
                type='email'
                className='style-input'
                onChange={this.handleChange}
              />
              <span className='focus-border'></span>
            </div>
            <div className='input-effect col-3 '>
              <label>Password</label>
              <input
                name='password'
                type='password'
                className='style-input'
                onChange={this.handleChange}
              />
              <span className='focus-border'></span>
            </div>

            <button
              className='btn'
              onClick={() => {
                this.handleRequest()
              }}
            >
              Sign Up
            </button>
          </>
        )

      case 'signin':
        return (
          <>
            <div className='input-effect col-3 '>
              <label>E-Mail</label>
              <input
                name='email'
                type='email'
                className='style-input'
                onChange={this.handleChange}
              />
              <span className='focus-border'></span>
            </div>
            <div className='input-effect col-3 '>
              <label>Password</label>
              <input
                name='password'
                type='password'
                className='style-input'
                onChange={this.handleChange}
              />
              <span className='focus-border'></span>
            </div>

            <button
              className='btn'
              onClick={() => {
                this.handleRequest()
              }}
            >
              Sign In
            </button>
          </>
        )

      default:
        break
    }
  }

  render() {
    return (
      <>
        <div className='auth-container'>
          <div className='wrapper'>
            <div className='left-auth'>
              <span className='base-quiz-text-area'>
                <h2 className='base-text'>Base</h2>
                <h2 className='quiz-text'>QUIZ</h2>
              </span>
              <p className='a-fun-place-text'>
                A fun place to create quiz and learn new things.
              </p>
              <img
                src='media/undraw_studying_s3l7.svg'
                alt='vector'
                className='vector-img'
              />
            </div>
            {/* right side area */}
            <div className='right-auth'>
              <div className='user-admin-area'>
                <span
                  onClick={() => {
                    this.setState({ viewTo: 'users' })
                  }}
                  className={
                    this.state.viewTo === 'users'
                      ? 'user-admin-area-span active-underline'
                      : 'user-admin-area-span'
                  }
                >
                  User
                </span>
                <span className='big-text or'>or</span>
                <span
                  onClick={() => {
                    this.setState({ viewTo: 'admins' })
                  }}
                  className={
                    this.state.viewTo === 'admins'
                      ? 'user-admin-area-span active-underline'
                      : 'user-admin-area-span'
                  }
                >
                  Admin
                </span>
              </div>
              <div className='btn-class-area'>
                <span
                  className='big-text big-text-active'
                  onClick={() => {
                    this.setState({ viewAuth: 'signin' })
                  }}
                  className={
                    this.state.viewAuth === 'signin'
                      ? 'big-text active-underline'
                      : 'big-text'
                  }
                >
                  Sign In
                </span>
                <span className='big-text or'>or</span>
                <span
                  className='big-text'
                  onClick={() => {
                    this.setState({ viewAuth: 'signup' })
                  }}
                  className={
                    this.state.viewAuth === 'signup'
                      ? 'big-text active-underline'
                      : 'big-text'
                  }
                >
                  Sign Up
                </span>
              </div>
              <div className='input-container'>{this.displayInput()}</div>
            </div>
          </div>
        </div>
      </>
    )
  }
}

function mapStateToProps(state) {
  return state.user
}

export default connect(mapStateToProps)(withRouter(Auth))
