import { SET_QUIZZES, SET_USERS, DELETE_QUIZ, INCREASE_MARK } from '../types/index'

function setQuizzes(payload) {
  return {
    type: SET_QUIZZES,
    payload: payload
  }
}

function setDeleteQuiz(payload) {
  return {
    type: DELETE_QUIZ,
    payload: payload
  }
}

export function setUsers(payload) {
  return {
    type: SET_USERS,
    payload: payload
  }
}

export function increaseMark(payload) {
  return {
    type: INCREASE_MARK,
    payload: payload
  }
}

export function fetchQuizzes() {
  return dispatch => {
    fetch('/api/v1/quizzes')
      .then(res => res.json())
      .then(quizzes => {
        if (quizzes.success) {
          dispatch(setQuizzes(quizzes))
        }
      })
  }
}

export function fetchUsers() {
  return dispatch => {
    fetch('/api/v1/users/me')
      .then(res => res.json())
      .then(users => {
        if (users.success) {
          dispatch(setUsers(users))
        }
      })
  }
}

export function deleteQuiz(id) {
  return dispatch => {
    fetch(`/api/v1/quizzes/${id}`, {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.token
      }
    })
      .then(res => res.json())
      .then(res => {
        if (res.success) {
          dispatch(setDeleteQuiz(id))
        }
      })
  }
}
