import React from 'react'
import { withRouter, Route, Switch } from 'react-router-dom'
import { connect } from 'react-redux'

import Auth from './authentication/Auth'
import CreateQuiz from './admin/CreateQuiz'
import ShowQuiz from './user/ShowQuiz'
import Setting from './user/Setting'
import ListQuizCreated from './admin/ListQuizCreated'
import QuizEdit from './admin/QuizEdit'
import FourOFour from './authentication/FourOFour'

function AdminRoutes() {
  return (
    <>
      <Route exact path='/admin/quiz/created' component={ListQuizCreated} />
      <Route exact path='/admin/quiz/create' component={CreateQuiz} />
      <Route exact path='/admin/quiz/update' component={CreateQuiz} />
      <Route exact path='/admin/quiz/edit/:id' component={QuizEdit} />
    </>
  )
}

function UserRoutes() {
  return (
    <>
      <Route exact path='/' component={ShowQuiz} />
      <Route exact path='/user/setting' component={Setting} />
    </>
  )
}

class App extends React.Component {
  constructor() {
    super()
    this.state = {}
  }

  render() {
    return (
      <Switch>
        <Route exact path='/auth' component={Auth} />
        {this.props.user === 'admins' ? (
          <AdminRoutes />
        ) : this.props.user === 'users' ? (
          <UserRoutes />
        ) : (
          <FourOFour />
        )}
      </Switch>
    )
  }
}
function mapStateToProps(state) {
  return state.user
}

export default connect(mapStateToProps)(withRouter(App))
