import React from 'react'
import { withRouter } from 'react-router-dom'

import Header from './Header'

class Setting extends React.Component {
  constructor() {
    super()
    this.state = {}
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value })
  }

  handleSubmit = event => {
    event.preventDefault()
    fetch('/api/v1/users/' + localStorage.id, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.token
      },
      body: JSON.stringify(this.state)
    })
      .then(res => res.json())
      .then(res => {
        alert(res.msg)
        this.props.history.push('/')
      })
  }

  async componentDidMount() {
    let response = await fetch('/api/v1/users/me', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.token
      }
    });

    let parsedData = await response.json();
    this.setState({...parsedData.user})
  }

  render() {
    return (
      <>
        <Header />
        <div className='wrapper setting-container'>
          <form onSubmit={this.handleSubmit} className='create-quiz'>
            <label className='crate-new-label' htmlFor=''>
              Full Name
            </label>
            <input
              type='text'
              name='username'
              onChange={this.handleChange}
              className='input-classic'
              value={this.state.username}
            />
            <label className='crate-new-label' htmlFor=''>
              E-mai
            </label>
            <input
              type='email'
              name='email'
              className='input-classic'
              onChange={this.handleChange}
              value={this.state.email}
            />                 

            <button type='submit' className='btn'>
              Update
            </button>
            <button
              className='logout'
              onClick={() => {
                localStorage.clear()
                this.props.history.push('/auth')
              }}
            >
              Logout
            </button>
          </form>
        </div>
      </>
    )
  }
}

export default withRouter(Setting)
